#ThreeDCarouseldiv {
  height: 700px;
  overflow: hidden;
  display: flex;
  background: #111;
  perspective: 1000px;
  transform-style: preserve-3d;
}

#drag-container,
#spin-container {
  position: relative;
  display: flex;
  margin: auto;
  transform-style: preserve-3d;
  transform: rotateX(-10deg);
}
#spin-container {
  -webkit-animation: spinRevert 60s infinite linear;
          animation: spinRevert 60s infinite linear;
}

#drag-container img,
#drag-container video {
  transform-style: preserve-3d;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  line-height: 200px;
  font-size: 50px;
  text-align: center;
  box-shadow: 0 0 8px #fff;
  -webkit-box-reflect: below 10px
    linear-gradient(transparent, transparent, #0005);
}

#drag-container img:hover,
#drag-container video:hover {
  box-shadow: 0 0 15px #fffd;
  -webkit-box-reflect: below 10px
    linear-gradient(transparent, transparent, #0007);
}

#drag-container p {
  font-family: Serif;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  color: #fff;
}

#ground {
  width: 900px;
  height: 900px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  background: -webkit-radial-gradient(
    center center,
    farthest-side,
    #9993,
    transparent
  );
}

@-webkit-keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

@keyframes spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}
@-webkit-keyframes spinRevert {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}
@keyframes spinRevert {
  from {
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

.journey {
  font-family: "Raleway", sans-serif;
  background-image: radial-gradient(circle at center, #899dc4, #495d84);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background: radial-gradient(ellipse at bottom, #1b2735, #090a0f);
  font-weight: 300;
}

.journeyall {
  display: flex;
  perspective: 10px;
  transform: perspective(300px) rotateX(20deg);
  will-change: perspective;
  perspective-origin: center center;
  transition: all 1.3s ease-out;
  justify-content: center;
  transform-style: preserve-3d;
}

.journeyall:hover {
  perspective: 1000px;
  transition: all 1.3s ease-in;
  transform: perspective(10000px) rotateX(0deg);
}
.journeyall:hover .journeytext {
  opacity: 1;
}
.journeyall:hover > div {
  opacity: 1;
  transition-delay: 0s;
}
.journeyall:hover .journeyexplainer {
  opacity: 0;
}

.journeyleft,
.journeycenter,
.journeyright,
.journeylefter,
.journeyrighter,
.heart-shape {
  width: 200px;
  height: 150px;
  transform-style: preserve-3d;
  border-radius: 10px;
  border: 1px solid #fff;
  box-shadow: 0 0 20px 5px rgba(100, 100, 255, 0.4);
  opacity: 0;
  transition: all 0.3s ease;
  transition-delay: 1s;
  position: relative;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #58d;
  cursor: pointer;
  background-blend-mode: color-burn;
}
@media only screen and (max-width: 600px) {
  .journeyleft,
  .journeycenter,
  .journeyright,
  .journeylefter,
  .journeyrighter,
  .heart-shape,
  .heart-shape {
    width: 60px;
    height: 45px;
  }
  .journeyall:hover {
    perspective: 350px;
    transition: all 1.3s ease-in;
    transform: perspective(350px) rotateX(0deg);
  }
  .journeytext {
    font-size: 7px;
    transform: translateY(12px) !important;
  }
  .journeyexplainer {
    font-size: 0.8rem !important;
  }
}
@media only screen and (min-width: 600px) and (max-width: 800px) {
  .journeyleft,
  .journeycenter,
  .journeyright,
  .journeylefter,
  .journeyrighter .heart-shape {
    width: 120px;
    height: 90px;
  }
  .journeyall:hover {
    perspective: 600px;
    transition: all 1.3s ease-in;
    transform: perspective(350px) rotateX(0deg);
  }
  .journeytext {
    font-size: 10px;
    transform: translateY(17px) !important;
  }
  .journeyexplainer {
    font-size: 1.2rem !important;
  }
}
.journeyleft:hover,
.journeycenter:hover,
.journeyright:hover,
.journeylefter:hover,
.journeyrighter:hover .heart-shape:hover {
  box-shadow: 0 0 30px 10px rgba(100, 100, 255, 0.6);
  background-color: #ccf;
}
@media only screen and (min-width: 800px) and (max-width: 1000px) {
  .journeyleft,
  .journeycenter,
  .journeyright,
  .journeylefter,
  .journeyrighter .heart-shape {
    width: 160px;
    height: 120px;
  }
  .journeyall:hover {
    perspective: 800px;
    transition: all 1.3s ease-in;
    transform: perspective(350px) rotateX(0deg);
  }
  .journeytext {
    font-size: 12px;
    transform: translateY(20px) !important;
  }
  .journeyexplainer {
    font-size: 1.6rem !important;
  }
}
.journeyleft:hover,
.journeycenter:hover,
.journeyright:hover,
.journeylefter:hover,
.journeyrighter:hover .heart-shape:hover {
  box-shadow: 0 0 30px 10px rgba(100, 100, 255, 0.6);
  background-color: #ccf;
}

.journeytext {
  transform: translateY(30px);
  opacity: 0;
  transition: all 0.3s ease;
  bottom: 0;
  left: 5px;
  position: absolute;
  will-change: transform;
  color: #fff;
  text-shadow: 0 0 5px rgba(100, 100, 255, 0.6);
}

.journeylefter {
  transform: translateX(-60px) translateZ(-50px) rotateY(-10deg);
}

.journeyleft {
  transform: translateX(-30px) translateZ(-25px) rotateY(-5deg);
}

.journeycenter {
  opacity: 1;
}

.journeyright {
  transform: translateX(30px) translateZ(-25px) rotateY(5deg);
  /* background-size: 70%; */
}

.journeyrighter {
  transform: translateX(60px) translateZ(-50px) rotateY(10deg);
}
.heart-shape {
  transform: translateX(90px) translateZ(-75px) rotateY(15deg);
}
.journeyexplainer {
  font-weight: 300;
  font-size: 2rem;
  color: #fff;
  transition: all 0.6s ease;
  width: 100%;
