.heart-shape {
  position: relative;
  width: 150px;
  height: 150px;
  /* opacity: 0; */
  background-blend-mode: color-burn;
  transform: rotate(45deg);
}
.heart-shape:before,
.heart-shape:after {
  position: absolute;
  width: 150px;
  height: 150px;
  content: "";
  transform-style: preserve-3d;

  transition: all 0.3s ease;
  transition-delay: 1s;

  background-color: #58d;
  cursor: pointer;
  background-blend-mode: color-burn;
  border-radius: 50%;
}
.heart-shape:before {
  bottom: 0px;
  left: -100px;
}
.heart-shape:after {
  top: -100px;
  right: 0px;
}

