.grid-memcard {
  position: relative;
  background-color: #70cff3;
  border-radius: 5px;
  /* width: 25vw;
  height: 25vw; */
}

.memcard {
  background-color: #009dd9 !important;
  border-radius: 0;
  width: 100%;
  height: auto;
  border: 1px solid rgba(0, 0, 0, 0.125);
}
.memcard::before {
  background-color: #009dd9 !important;
  width: 100%;
  height: auto;
  /* height: 250px !important; */
}
.grid-memcard::after {
  content: "";
  background: -webkit-linear-gradient(59deg, #3a6073, #16222a);
  /* width: 250px;
  height: 250px; */
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(0) perspective(100px);
  transition: 0.4s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.grid-img {
  object-fit: cover;
  overflow: hidden;
  object-position: center;
  transform: rotateY(180deg) perspective(100px);
  transition: 0.4s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  padding: 1px;
  background-color: #70cff3;
  border-radius: 5px;
}

@media screen and (min-width: 400px) {
  .grid-img {
    padding: 4px;
  }
}

.grid-memcard-show::after {
  transform: rotateY(180deg) perspective(100px);
  /* width: 250px;
  height: 250px; */
}
.grid-memcard-show .grid-img {
  transform: rotateY(0) perspective(100px);
  /* width: 250px;
  height: 250px; */
}
.grid-memcard-finished {
  opacity: 0.5;
}

